import React, { useState, useEffect } from "react";

const NewComment = (props) => {
  const [commentInput, setCommentInput] = useState();
  const [isValid, setIsValid] = useState(false);

  const commentInputChangeHandler = (e) => {
    setCommentInput(e.target.value);
  };

  useEffect(() => {
    if (commentInput && commentInput.trim() !== "") {
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  }, [commentInput]);

  const formSubmissionHandler = (e) => {
    e.preventDefault();

    // 1. ëŒ“ê¸€ ì…ë ¥ê°’ í™•ì¸
    if (isValid) {
      // 2. ëŒ“ê¸€ ë“±ë¡
      props.addComment({ username: "bokjiho", content: commentInput });

      // 3. ëŒ“ê¸€ ì…ë ¥ ì¹¸ ë¹„ìš°ê¸°
      setCommentInput("");
      setIsValid(false);
      document.querySelector(`#comment-input${props.id}`).blur();
    }
  };

  // ëŒ“ê¸€ ì…ë ¥ì¹¸ì—ì„œ Enter í‚¤ ëˆŒë €ì„ ê²½ìš°ì— ëŒ“ê¸€ ë“±ë¡
  const keypressHandler = (e) => {
    if (e.keyCode === 13 && e.shiftKey === false) {
      e.preventDefault();
      document.querySelector(`#submit-button${props.id}`).click();
    }
  };

  const validButton = (
    <button
      id={"submit-button" + props.id}
      type="submit"
      style={{ opacity: "1", cursor: "pointer" }}
    >
      ê²Œì‹œ
    </button>
  );
  const invalidButton = <button type="submit">ê²Œì‹œ</button>;

  return (
    <section className="new-comment">
      <form
        key={props.id}
        id={"comment-form" + props.id}
        onSubmit={formSubmissionHandler}
      >
        <textarea
          id={"comment-input" + props.id}
          name="comment-input"
          placeholder="ëŒ“ê¸€ ë‹¬ê¸°..."
          value={commentInput}
          onChange={commentInputChangeHandler}
          onKeyDown={keypressHandler}
          ref={props.commentInputRef}
        ></textarea>
        {isValid ? validButton : invalidButton}
      </form>
    </section>
  );
};

export default NewComment;
